import React from 'react';
import './LoginFeild.css';
import {Button, TextField} from "@mui/material";
import {useNavigate} from "react-router-dom";

const LoginField = () => {
    const navigate = useNavigate();
    return (
        <div className="loginField">
            <div className="login_inputField">
                <TextField id="standard-basic" label="" variant="standard" sx={{width: 280}}/>
                <br/>
                <TextField id="standard-basic" label="" variant="standard" sx={{width: 280}}/>
            </div>
            <div className="login_linkField">
                <p className="login_findLink"></p>
                <p className="login_findNoLink">/</p>
                <p className="login_findLink"></p>
            </div>
            <div className="login_buttonField">
                <Button variant="contained" sx={{width: 280, borderRadius: 30}}></Button>
                <br/>
                <Button variant="contained" sx={{width: 280, borderRadius: 30}} onClick={()=>navigate("/policy")}></Button>
            </div>
        </div>
    );
};

export default LoginField;